body{
  background-color: #f3f3f3;
  color: rgb(33, 37, 41);
}

main{
  padding-left: 1rem;
  padding-right: 1rem;
}

.timeday{
  color: orangered;
}

.healthymeals{
  list-style-type: none;
  padding-left: 0;
  font-size: 1.2rem;
  border: 1px solid black;
  width: fit-content;
  padding: .5rem;
}

.healthymeal{
  display: inline;
  cursor: pointer;
}

#chosenmeal{
  color: dodgerblue;
}

.healthymeal:hover{
  color: orangered;
}

#chosenmeal:hover{
  color: orangered;
}

.healthymeals ::after{
  content: ' ';
}

.menus{
  list-style-type: none;
  padding-left: 0;
}

.menuplate{
  border: .5rem dashed skyblue;
  border-radius: 2rem;
  width: fit-content;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  text-align: center;
}

.allergy{
  width: fit-content;
  border: 1px solid black;
  padding: 1rem;
  margin-top: 1rem;
}

table, tr, td{
  border: 1px solid black;
  border-collapse: collapse;
}

td{
  padding: .1rem;
}

#altablename{
  text-align: center;
}

.desc, .credit{
  margin-left: -1rem;
  margin-right: -1rem;
  padding: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-top: 3px solid #f8f8f8;
}

.desc{
  background-color: white;
  margin-bottom: -1rem;
}

.credit{
  margin-bottom: 0;
  list-style-type: none;
  text-align: right;
  background-color: white;
}

.movedate{
  display: inline;
}

.setdate{
  text-align: center;
  padding-left: 0;
}

.submit, #movedatebutton, #addmenubutton{
  background-color: white;
  border: 1px solid rgb(33, 37, 41);
  border-radius: .2rem;
  cursor: pointer;
}

.addmenus{
  list-style-type: none;
  padding-left: 0;
}

.addmenuplate{
  display: inline-block;
  border: .5rem dashed deeppink;
  border-radius: 2rem;
  width: fit-content;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  text-align: center;
  margin-bottom: 2rem;
  margin-right: 2rem;
}

#addmenunum, #addmenusubmit{
  display: block;
  margin-bottom: 2rem;
}

.menuallergyinput{
  height: 4rem;
}